import { useId } from "react"

interface AnimatedSihLogoProps {
  className?: string
}

export function AnimatedSihLogo({ className }: AnimatedSihLogoProps) {
  const baseId = useId()
  const leftGradientId = `${baseId}-left`
  const rightGradientId = `${baseId}-right`
  const glowId = `${baseId}-glow`

  // Create 8 rays extending outward
  const rays = Array.from({ length: 8 }, (_, i) => {
    const angle = (Math.PI * 2 * i) / 8
    const innerRadius = 75
    const outerRadius = 110
    const cx = 100
    const cy = 80

    return {
      x1: cx + Math.cos(angle) * innerRadius,
      y1: cy + Math.sin(angle) * innerRadius,
      x2: cx + Math.cos(angle) * outerRadius,
      y2: cy + Math.sin(angle) * outerRadius,
    }
  })

  return (
    <div className={className || "relative h-full w-full"}>
      <svg viewBox="0 0 200 280" className="h-full w-full" role="img" aria-label="Smart India Hackathon Logo">
        <defs>
          {/* Orange gradient for left side */}
          <linearGradient id={leftGradientId} x1="40" y1="40" x2="100" y2="140" gradientUnits="userSpaceOnUse">
            <stop offset="0%" stopColor="#FF8C3B" />
            <stop offset="100%" stopColor="#FF6B1F" />
          </linearGradient>

          {/* Green gradient for right side */}
          <linearGradient id={rightGradientId} x1="100" y1="40" x2="160" y2="140" gradientUnits="userSpaceOnUse">
            <stop offset="0%" stopColor="#1FA859" />
            <stop offset="100%" stopColor="#0F8C45" />
          </linearGradient>

          {/* Glow effect */}
          <radialGradient id={glowId} cx="100" cy="80" r="85" gradientUnits="userSpaceOnUse">
            <stop offset="0%" stopColor="rgba(255,140,59,0.15)" />
            <stop offset="100%" stopColor="rgba(0,0,0,0)" />
          </radialGradient>
        </defs>

        {/* Glow circle background */}
        <circle cx="100" cy="80" r="80" fill={`url(#${glowId})`} opacity="0.5" />

        {/* Rays extending outward */}
        {rays.map((ray, i) => (
          <line
            key={`ray-${i}`}
            x1={ray.x1}
            y1={ray.y1}
            x2={ray.x2}
            y2={ray.y2}
            stroke="#4A5F74"
            strokeWidth="8"
            strokeLinecap="round"
            opacity="0.9"
          >
            <animate
              attributeName="opacity"
              values="0.5;1;0.5"
              dur="3s"
              begin={`${i * 0.15}s`}
              repeatCount="indefinite"
            />
          </line>
        ))}

        {/* LEFT BRAIN - Orange Circuit Pattern */}
        <g>
          {/* Outer shape */}
          <path
            d="M100 30C75 30 55 50 55 75C55 85 58 95 63 103C68 111 70 120 70 130C70 145 75 158 85 165L100 170V30Z"
            fill={`url(#${leftGradientId})`}
            stroke="#D44E00"
            strokeWidth="2"
            strokeLinejoin="round"
          />

          {/* Vertical circuit lines - left side */}
          <g stroke="#FFE6D3" strokeWidth="2" fill="none" strokeLinecap="round">
            {/* Main vertical lines */}
            <line x1="65" y1="50" x2="65" y2="160" opacity="0.8" />
            <line x1="73" y1="45" x2="73" y2="165" opacity="0.7" />
            <line x1="81" y1="48" x2="81" y2="162" opacity="0.8" />
            <line x1="89" y1="50" x2="89" y2="160" opacity="0.7" />
            <line x1="97" y1="52" x2="97" y2="158" opacity="0.8" />

            {/* Horizontal connectors creating circuit pattern */}
            <path d="M65 60L89 60" opacity="0.7" />
            <path d="M73 70L97 70" opacity="0.6" />
            <path d="M65 80L89 80" opacity="0.7" />
            <path d="M73 90L97 90" opacity="0.6" />
            <path d="M65 100L89 100" opacity="0.7" />
            <path d="M73 110L97 110" opacity="0.6" />
            <path d="M65 120L89 120" opacity="0.7" />
            <path d="M73 130L97 130" opacity="0.6" />
            <path d="M65 140L89 140" opacity="0.7" />
            <path d="M73 150L97 150" opacity="0.6" />

            {/* Diagonal connections for maze effect */}
            <path d="M73 60L81 70" opacity="0.5" />
            <path d="M89 70L81 80" opacity="0.5" />
            <path d="M73 90L81 100" opacity="0.5" />
            <path d="M89 100L81 110" opacity="0.5" />
            <path d="M73 120L81 130" opacity="0.5" />
            <path d="M89 130L81 140" opacity="0.5" />
          </g>

          {/* Accent circles on left */}
          <circle cx="70" cy="85" r="3" fill="#FFE6D3" opacity="0.8" />
          <circle cx="88" cy="120" r="3" fill="#FFE6D3" opacity="0.8" />
          <circle cx="75" cy="145" r="2.5" fill="#FFE6D3" opacity="0.7" />
        </g>

        {/* RIGHT BRAIN - Green Binary Code Pattern */}
        <g>
          {/* Outer shape */}
          <path
            d="M100 30C125 30 145 50 145 75C145 85 142 95 137 103C132 111 130 120 130 130C130 145 125 158 115 165L100 170V30Z"
            fill={`url(#${rightGradientId})`}
            stroke="#0F7442"
            strokeWidth="2"
            strokeLinejoin="round"
          />

          {/* Binary code grid - 1s and 0s */}
          <g
            fontFamily="'IBM Plex Mono', 'Courier New', monospace"
            fontSize="11"
            fontWeight="700"
            fill="#FFFFFF"
            textAnchor="middle"
            opacity="0.95"
          >
            {/* Column 1 */}
            <text x="111" y="58">
              1
            </text>
            <text x="111" y="72">
              0
            </text>
            <text x="111" y="86">
              1
            </text>
            <text x="111" y="100">
              0
            </text>
            <text x="111" y="114">
              1
            </text>
            <text x="111" y="128">
              0
            </text>
            <text x="111" y="142">
              1
            </text>

            {/* Column 2 */}
            <text x="125" y="58">
              0
            </text>
            <text x="125" y="72">
              1
            </text>
            <text x="125" y="86">
              0
            </text>
            <text x="125" y="100">
              1
            </text>
            <text x="125" y="114">
              0
            </text>
            <text x="125" y="128">
              1
            </text>
            <text x="125" y="142">
              0
            </text>

            {/* Column 3 */}
            <text x="139" y="58">
              1
            </text>
            <text x="139" y="72">
              0
            </text>
            <text x="139" y="86">
              1
            </text>
            <text x="139" y="100">
              1
            </text>
            <text x="139" y="114">
              0
            </text>
            <text x="139" y="128">
              1
            </text>
            <text x="139" y="142">
              0
            </text>
          </g>
        </g>

        {/* BULB BASE - Light bulb shape */}
        <g>
          {/* Main bulb body */}
          <ellipse cx="100" cy="195" rx="35" ry="28" fill="#4A6B84" stroke="#3A5770" strokeWidth="1.5" />

          {/* Bulb threading/screw connector */}
          <rect x="92" y="172" width="16" height="12" rx="2" fill="#3A5770" />

          {/* Bulb top cap */}
          <rect x="88" y="168" width="24" height="5" rx="2.5" fill="#2A4560" />
        </g>

        {/* SIH Text */}
        <text
          x="100"
          y="255"
          textAnchor="middle"
          fontFamily="'Rajdhani', 'Arial', sans-serif"
          fontSize="28"
          fontWeight="700"
          fill="#4A5F74"
          letterSpacing="2"
        >
          SIH
        </text>
      </svg>
    </div>
  )
}
